'use client';
import React, { useState, useEffect, useMemo } from 'react';
import axios from 'axios';

const ProgramCourses = () => {
  const [periods, setPeriods] = useState([]);
  const [courses, setCourses] = useState([]);
  const [selectedPeriod, setSelectedPeriod] = useState('');
  const [availableCoursesSearch, setAvailableCoursesSearch] = useState('');
  const [periodCoursesSearch, setPeriodCoursesSearch] = useState('');
  const [periodCourses, setPeriodCourses] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch periods and courses from the API
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const [periodsResponse, coursesResponse] = await Promise.all([
          axios.get('http://localhost:9921/periods'),
          axios.get('http://localhost:9921/courses'),
        ]);
        
        // Improved period name formatting
        const safePeriods = periodsResponse.data.map((period, index) => {
          // Default naming if no specific name is provided
          let formattedName = period.name || `Y${Math.floor(index/2) + 1}S${(index % 2) + 1}`;
          
          return {
            ...period,
            uniqueId: period.id ? `period-${period.id}` : `period-${index}`,
            name: formattedName
          };
        });

        const safeCourses = coursesResponse.data.map((course, index) => ({
          ...course,
          uniqueId: course.id ? `course-${course.id}` : `course-${index}`,
          courseCode: course.courseCode || course.code || `COURSE-${index + 1}`,
          name: course.name || `Unnamed Course ${index + 1}`,
          department: course.department || 'Unknown Department'
        }));
        
        setPeriods(safePeriods);
        setCourses(safeCourses);
        
        // Set first period as selected, with fallback
        if (safePeriods.length > 0) {
          setSelectedPeriod(safePeriods[0].uniqueId);
        }
      } catch (err) {
        console.error('Detailed Fetch Error:', err);
        setError(`Failed to load data: ${err.message}`);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // Filter available courses
  const filteredAvailableCourses = useMemo(() => {
    const currentPeriodCourses = periodCourses[selectedPeriod] || [];
    return courses.filter(
      (course) =>
        course.uniqueId && 
        !currentPeriodCourses.some((c) => c.uniqueId === course.uniqueId) &&
        course.courseCode.toLowerCase().includes(availableCoursesSearch.toLowerCase())
    );
  }, [availableCoursesSearch, selectedPeriod, periodCourses, courses]);

  // Filter courses for the selected period
  const filteredPeriodCourses = useMemo(() => {
    return (periodCourses[selectedPeriod] || []).filter(
      (course) =>
        course.uniqueId && 
        course.courseCode.toLowerCase().includes(periodCoursesSearch.toLowerCase())
    );
  }, [periodCoursesSearch, selectedPeriod, periodCourses]);

  // Add course to the selected period
  const addCourseToPeriod = (course) => {
    setPeriodCourses((prev) => ({
      ...prev,
      [selectedPeriod]: [...(prev[selectedPeriod] || []), course],
    }));
  };

  // Remove course from the selected period
  const removeCourseFromPeriod = (courseToRemove) => {
    setPeriodCourses((prev) => ({
      ...prev,
      [selectedPeriod]: prev[selectedPeriod]?.filter((course) => course.uniqueId !== courseToRemove.uniqueId) || [],
    }));
  };

  // Submit selected courses
  const submitSelectedCourses = async () => {
    try {
      // Find the selected period's actual ID (removing the 'period-' prefix)
      const periodId = selectedPeriod.replace('period-', '');
      
      // Hardcoded programId - you would typically pass this as a prop or from context
      const programId = '1'; // Replace with actual program ID

      // Extract course IDs, removing the 'course-' prefix
      const courseIds = (periodCourses[selectedPeriod] || []).map(course => 
        course.uniqueId.replace('course-', '')
      );

      // Submit courses sequentially
      for (const courseId of courseIds) {
        await axios.post('http://localhost:9921/programCourse', {
          programId,
          periodId,
          courseId
        });
      }

      // Clear the selected courses after successful submission
      setPeriodCourses(prev => ({
        ...prev,
        [selectedPeriod]: []
      }));

      // Optional: Add a success notification
      alert('Courses submitted successfully!');
    } catch (err) {
      console.error('Submission Error:', err);
      alert('Failed to submit courses');
    }
  };

  // Render loading state
  if (loading) return <div className="p-6 text-center">Loading...</div>;

  // Render error state
  if (error) return <div className="p-6 text-red-500">{error}</div>;

  // Render no data state
  if (periods.length === 0) {
    return <div className="p-6 text-center text-gray-500">No periods available</div>;
  }

  return (
    <div className="container mx-auto p-6">
      <div className="mb-6">
        <h1 className="text-2xl font-bold">Program Courses Dashboard</h1>
        <p className="text-gray-600">Manage courses for each period of the program.</p>
      </div>

      <div className="mb-4">
        <label htmlFor="period-select" className="block text-sm font-medium text-gray-700">
          Select Period
        </label>
        <select
          id="period-select"
          value={selectedPeriod}
          onChange={(e) => setSelectedPeriod(e.target.value)}
          className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm"
        >
          {periods.map((period) => (
            <option 
              key={period.uniqueId} 
              value={period.uniqueId}
            >
              {period.name}
            </option>
          ))}
        </select>
      </div>

      <div className="grid grid-cols-2 gap-6">
        {/* Available Courses Column */}
        <div className="border rounded-lg shadow-md">
          <div className="p-4 border-b">
            <h2 className="text-lg font-semibold">Available Courses</h2>
            <input
              type="text"
              placeholder="Search available courses"
              value={availableCoursesSearch}
              onChange={(e) => setAvailableCoursesSearch(e.target.value)}
              className="mt-2 block w-full p-2 border border-gray-300 rounded-md shadow-sm"
            />
          </div>
          <div className="h-96 overflow-y-auto p-4">
            {filteredAvailableCourses.map((course) => (
              <div
                key={course.uniqueId}
                className="flex justify-between items-center p-2 border-b hover:bg-gray-100"
              >
                <div>
                  <p className="font-medium">{course.courseCode}</p>
                  <p className="text-sm text-gray-500">{course.name}</p>
                </div>
                <button
                  onClick={() => addCourseToPeriod(course)}
                  className="text-green-600 hover:text-green-800"
                >
                  Add
                </button>
              </div>
            ))}
            {filteredAvailableCourses.length === 0 && (
              <p className="text-center text-gray-500">No courses found</p>
            )}
          </div>
        </div>

        {/* Period Courses Column */}
        <div className="border rounded-lg shadow-md">
          <div className="p-4 border-b">
            <h2 className="text-lg font-semibold">Courses for Selected Period</h2>
            <input
              type="text"
              placeholder="Search added courses"
              value={periodCoursesSearch}
              onChange={(e) => setPeriodCoursesSearch(e.target.value)}
              className="mt-2 block w-full p-2 border border-gray-300 rounded-md shadow-sm"
            />
          </div>
          <div className="h-96 overflow-y-auto p-4">
            {filteredPeriodCourses.map((course) => (
              <div
                key={course.uniqueId}
                className="flex justify-between items-center p-2 border-b hover:bg-gray-100"
              >
                <div>
                  <p className="font-medium">{course.courseCode}</p>
                  <p className="text-sm text-gray-500">{course.name}</p>
                </div>
                <button
                  onClick={() => removeCourseFromPeriod(course)}
                  className="text-red-600 hover:text-red-800"
                >
                  Remove
                </button>
              </div>
            ))}
            {filteredPeriodCourses.length === 0 && (
              <p className="text-center text-gray-500">No courses added</p>
            )}
          </div>
        </div>
      </div>

      {/* Submit Button */}
      <div className="mt-6 text-center">
        <button
          onClick={submitSelectedCourses}
          disabled={!(periodCourses[selectedPeriod] && periodCourses[selectedPeriod].length > 0)}
          className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed"
        >
          Submit Selected Courses
        </button>
      </div>
    </div>
  );
};

export default ProgramCourses;